body {
    margin:0;
}
#root {

    /* Color Variables */
    --background: #1A1826;
    --darkblue: #323859;
    --lightblue: #63C6EF;
    --lightgreen: #85D982;
    --orange: #f2a766;
    --heroimagebottom: #010108;
    --lightblueopacity: rgba(99, 198, 239, 0.15);

    /* Sizing */
    margin:0;
    padding:0;
    display: flex;
    justify-content: center;

    /* Styling */
    background: var(--background);
    overflow:hidden;
}
html {
    margin:0
}
::-webkit-scrollbar {
    width: 8px;
    
}
::-webkit-scrollbar-track {
    background: rgb(1,1,8);
    background: linear-gradient(180deg, rgba(1,1,8,1) 0%, rgba(50,56,89,1) 100%);
}
::-webkit-scrollbar-thumb {
    background: rgb(50,56,89);
    background: linear-gradient(180deg, rgba(50,56,89,1) 0%, rgba(50,56,89,1) 100%);
  }

.general-orange {
    color: var(--orange)
}
.general-lightgreen {
    color: var(--lightgreen)
}
.general-lightblue {
    color: var(--lightblue)
}
.general-darkblue {
    color: var(--darkblue)
}


/* navbar */

#pc-view-navbar-home {
    display:none;
}
#pc-view-navbar-services {
    display:block;
}